import { AfterViewInit, Component, ElementRef, EventEmitter, Input, Output } from '@angular/core';
import { BcOffCanvasBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

@Component({
  selector: 'bc-off-canvas',
  templateUrl: './bc-off-canvas.component.html',
})
export class BcOffCanvasComponent implements AfterViewInit {
  bcOffCanvasBehavior;
  constructor(private ref: ElementRef, private helperService: BcHelperService) {
    this.bcOffCanvasBehavior = new BcOffCanvasBehavior();
  }
  /**
   * Id del offCanvas.
   *
   * @type {string}
   * @memberof BcOffCanvasComponent
   */
  @Input() id: string = this.helperService.getId(this);

  /**
   * Tipo de offCanvas.
   *
   * @type {string}
   * @memberof BcOffCanvasComponent
   */
  @Input() tipoOffcanvas: 'horizontal' | 'vertical' = 'vertical';

  /**
   * Backdrop Apagado.
   *
   * @type {string}
   * @memberof BcOffCanvasComponent
   */
  @Input() backdrop: 'off' | '' = '';

  /**
   * Define la altura del componente horizontal
   *
   * @type {string}
   * @memberof BcOffCanvasComponent
   */
  @Input() alturaHorizontal: string = '';

    /**
   * Se ejecuta cuando el offCanvas es cerrado
   *
   * @type {EventEmitter<any>}
   * @memberof BcOffCanvasComponent
   */
     @Output() closed: EventEmitter<any> = new EventEmitter<any>();

  ngAfterViewInit() {
    this.bcOffCanvasBehavior.setId(this.id);
    this.bcOffCanvasBehavior.setUp();
  }

  /**
   * Abre el offcanvas.
   *
   * @memberof BcOffCanvasComponent
   */
  openOffCanvas() {
    this.bcOffCanvasBehavior.open();
  }
    /**
   * Cierra el offcanvas.
   *
   * @param {any} param
   * @memberof BcOffCanvasComponent
   */
  closeOffCanvas(param : any) {
    this.bcOffCanvasBehavior.close();
    this.closed.emit(param);
  }
}